/*
    ===============================
    COMMON CODE
    ===============================
*/
@mixin accessible-hide() {
  clip        : rect(0 0 0 0);
  clip-path   : inset(50%);
  height      : 1px;
  overflow    : hidden;
  position    : absolute;
  white-space : nowrap;
  width       : 1px;
}

@mixin checkbox-hide {
  border   : 0;
  clip     : rect(0 0 0 0);
  height   : 1px;
  margin   : -1px;
  overflow : hidden;
  padding  : 0;
  position : absolute;
  width    : 1px;
}

$z-index : (
  footer: 10,
  header: 15,
  menu: 2,
  localtasks: 3,
);

@function z-index($key) {
  @return map-get($z-index, $key);
}

@mixin z-index($key) {
  z-index : z-index($key);
}

/*
    ===============================
    TYPOGRAPHY
    ===============================
*/

/* Font families */
@mixin sans-heading {
  font-family : "Cabinet Grotesk", sans-serif;
}

@mixin sans-body {
  font-family : "Noto Sans", sans-serif;
}

/*
    ===============================
    MISC
    ===============================
*/
@mixin placeholder($color) {
  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color   : $color;
    opacity : 1;
  }
  &::-moz-placeholder { /* Firefox 19+ */
    color   : $color;
    opacity : 1;
  }
  &:-ms-input-placeholder { /* IE 10+ */
    color   : $color;
    opacity : 1;
  }
  &:-moz-placeholder { /* Firefox 18- */
    color   : $color;
    opacity : 1;
  }
}

@mixin selection-background($color) {
  &::selection {
    background : $color;
  }
  &::-moz-selection {
    background : $color;
  }
}
